/* ============================================
   ðŸš€ OPTIMIZED DEGREE CARD
   ============================================ */

.degree-card {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 30px;
  overflow: visible; /* Changed from hidden to allow 3D pop-out */

  /* ðŸš€ OPTIMIZATION: Hardware Acceleration */
  will-change: transform;
  transform: translateZ(0);
  transform-style: preserve-3d;
  perspective: 1500px;

  /* ðŸš€ OPTIMIZATION: Lazy Render Off-screen cards */
  content-visibility: auto;
  contain-intrinsic-size: 0 300px; /* Estimate height to prevent scroll jump */

  cursor: pointer;
  background: rgba(255, 255, 255, 0.05);

  /* ðŸš€ OPTIMIZATION: Main blur only. Removed inner blurs. */
  backdrop-filter: blur(20px) saturate(180%) brightness(1.1);
  -webkit-backdrop-filter: blur(20px) saturate(180%) brightness(1.1);

  border-radius: 20px;
  padding: 25px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.12), 0 5px 15px rgba(0, 0, 0, 0.08);

  /* ðŸš€ OPTIMIZATION: Specific transitions only */
  transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.4s ease,
    background 0.4s ease, border-color 0.4s ease;
  z-index: 1;
}

/* Enhanced hover effect with 3D lift */
.degree-card:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.2), 0 10px 25px rgba(0, 0, 0, 0.15);
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.25);
}

/* Glass reflection overlay strip */
.degree-card::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -10%;
  width: 40%;
  height: 200%;
  background: linear-gradient(
    125deg,
    transparent 0%,
    rgba(255, 255, 255, 0.1) 40%,
    rgba(255, 255, 255, 0.3) 50%,
    rgba(255, 255, 255, 0.1) 60%,
    transparent 100%
  );
  transform: rotate(25deg);
  pointer-events: none;
  opacity: 0.2;
  z-index: 3;
  /* ðŸš€ OPTIMIZATION: Only animate specific properties */
  transition: left 0.6s ease, opacity 0.6s ease;
}

.degree-card:hover::before {
  opacity: 0.1;
  left: 120%; /* Move completely off card */
}

/* Corner accents */
.degree-card::after,
.corner-accent-bottom {
  content: "";
  position: absolute;
  width: 60px;
  height: 60px;
  border-color: rgba(255, 255, 255, 0.2);
  border-style: solid;
  transition: width 0.4s ease, height 0.4s ease, border-color 0.4s ease;
  z-index: 2;
  pointer-events: none;
}

.degree-card::after {
  top: 0;
  left: 0;
  border-width: 2px 0 0 2px; /* Top Left */
  border-radius: 20px 0 0 0;
}

.corner-accent-bottom {
  bottom: 0;
  right: 0;
  border-width: 0 2px 2px 0; /* Bottom Right */
  border-radius: 0 0 20px 0;
}

.degree-card:hover::after,
.degree-card:hover .corner-accent-bottom {
  width: 80px;
  height: 80px;
  border-color: rgba(255, 255, 255, 0.4);
}

/* 3D Floating Elements */
.floating-element {
  position: absolute;
  background: radial-gradient(
    circle at center,
    rgba(255, 255, 255, 0.15) 0%,
    transparent 70%
  );
  border-radius: 50%;
  pointer-events: none;
  /* ðŸš€ OPTIMIZATION: Use opacity/transform only */
  animation: float 15s infinite ease-in-out;
  opacity: 0.5;
  z-index: 1;
  will-change: transform; /* Hint browser for smooth animation */
}

.floating-element:nth-child(1) {
  top: 10%;
  left: 10%;
  width: 50px;
  height: 50px;
  animation-delay: 0s;
}
.floating-element:nth-child(2) {
  top: 50%;
  right: 15%;
  width: 70px;
  height: 70px;
  animation-delay: -5s;
}
.floating-element:nth-child(3) {
  bottom: 20%;
  left: 30%;
  width: 55px;
  height: 55px;
  animation-delay: -10s;
}

@keyframes float {
  0%,
  100% {
    transform: translate3d(0, 0, 10px);
    opacity: 0.5;
  }
  50% {
    transform: translate3d(-10px, 20px, 15px);
    opacity: 0.6;
  }
}

/* ============================================
   ðŸŽ¨ INNER CONTENT
   ============================================ */
.body-header {
  max-width: inherit;
  display: flex;
  border-radius: 15px 15px 0px 0px;
  padding: 15px;
  transform: translate3d(0, 0, 20px); /* Reduced Z for better performance */

  /* ðŸš€ OPTIMIZATION: Removed nested backdrop-filter. Uses pure color opacity */
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0.05)
  );

  transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1), background 0.4s ease;
  position: relative;
  z-index: 5;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.degree-card:hover .body-header {
  transform: translate3d(0, 0, 40px);
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.15),
    rgba(255, 255, 255, 0.08)
  );
}

.body-header-title {
  width: 80%;
  position: relative;
  z-index: 6;
}
.body-header-duration {
  width: 20%;
  position: relative;
  z-index: 6;
}

.duration {
  font-family: "Google Sans Regular";
  font-size: 16px;
  float: right;
  background: rgba(255, 255, 255, 0.1); /* Simple transparency */
  padding: 6px 12px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  transition: transform 0.3s ease, background 0.3s ease;
}

.degree-card:hover .duration {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
}

.body-content {
  max-width: inherit;
  border-radius: 0px 0px 15px 15px;
  padding: 20px;
  transform: translate3d(0, 0, 10px);

  /* ðŸš€ OPTIMIZATION: Removed nested blur */
  background: rgba(255, 255, 255, 0.03);

  transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1), background 0.4s ease;
  position: relative;
  z-index: 5;
}

.degree-card:hover .body-content {
  transform: translate3d(0, 0, 30px);
  background: rgba(255, 255, 255, 0.06);
}

.content-list {
  margin: 5px;
  padding-left: 20px;
  font-family: "Google Sans Regular";
  line-height: 1.8;
}

.content-list::before {
  content: "â–¸";
  position: absolute;
  left: 0;
  color: rgba(255, 255, 255, 0.5);
  transition: transform 0.3s ease, color 0.3s ease;
}

.degree-card:hover .content-list::before {
  color: rgba(255, 255, 255, 0.8);
  transform: translateX(3px);
}

.card-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 8px;
  /* ðŸš€ OPTIMIZATION: Text shadow is expensive, removed spread */
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}

.card-subtitle {
  font-size: 18px;
  opacity: 0.9;
}

/* Image Container */
.degree-card [class*="css-"],
.degree-card img {
  transform-style: preserve-3d;
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: 12px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  position: relative;
  z-index: 6;
  backface-visibility: hidden;
}

.degree-card:hover [class*="css-"],
.degree-card:hover img {
  transform: translate3d(0, 0, 50px) scale(1.05);
  border-color: rgba(255, 255, 255, 0.2);
}

/* ============================================
   ðŸ“± MOBILE OPTIMIZATION
   ============================================ */
@media (max-width: 768px) {
  .degree-card {
    flex-direction: column;
    padding: 20px;
    margin: 15px 10px;
    /* ðŸš€ OPTIMIZATION: Remove perspective on mobile to save battery */
    perspective: none;
    transform-style: flat;

    /* Re-add blur here since we don't have heavy 3D transforms */
    backdrop-filter: blur(15px);
  }

  .floating-element {
    display: none;
  } /* Hide decoration */

  /* ðŸš€ OPTIMIZATION: Disable 3D transforms on mobile */
  .degree-card:hover .body-header,
  .degree-card:hover .body-content,
  .degree-card:hover img {
    transform: none;
  }

  .degree-card:hover {
    transform: translateY(-5px);
  }

  .body-header {
    flex-direction: column;
    padding: 12px;
    border-radius: 12px 12px 0 0;
    transform: none !important;
  }

  .body-content {
    transform: none !important;
  }

  .body-header-title,
  .body-header-duration {
    width: 100%;
    margin-bottom: 5px;
  }
  .duration {
    float: left;
    font-size: 14px;
  }
  .card-title {
    font-size: 20px;
  }
  .card-subtitle {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .degree-card {
    margin: 10px 5px;
    padding: 15px;
  }
  .card-title {
    font-size: 18px;
  }
  .card-subtitle {
    font-size: 14px;
  }
}

/* ðŸš€ ACCESSIBILITY: Reduce motion */
@media (prefers-reduced-motion: reduce) {
  .degree-card,
  .degree-card *,
  .floating-element {
    animation: none !important;
    transition: none !important;
    transform: none !important;
  }
}
